import { Component, OnInit } from '@angular/core';
import { HttpService } from '../../../services/http.service';

@Component({
  selector: 'app-mi-perfil',
  templateUrl: './mi-perfil.component.html',
  styleUrls: ['./mi-perfil.component.css']
})
export class MiPerfilComponent implements OnInit {

  public id: number;
  public profile = {};
  public user = {
    first_name: "",
    email: "",
    last_name: "",
    nombre_cliente: "",
    role_name: "",
    user_name: "",
    user_end : ""
  }

  constructor( private http : HttpService) { }

  ngOnInit() {
    this.http.decodedToken().subscribe(
      data => {
        this.id = data.token.id
        this.profileUser(this.id)
      }
    );
    
  }

  profileUser(idProfile){
    this.http.profileUser(idProfile).subscribe(
      data => {
        console.log(data.data[0]);
        this.profile = data.data[0];
        // @ts-ignore
        this.user.first_name = this.profile.first_name;
        // @ts-ignore
        this.user.last_name = this.profile.last_name;
        // @ts-ignore
        this.user.user_name = this.profile.user_name;
        // @ts-ignore
        this.user.role_name = this.profile.role_name;
        // @ts-ignore
        this.user.user_end = this.profile.user_end;
        // @ts-ignore
        this.user.email = this.profile.email;
        // @ts-ignore
        this.user.nombre_cliente = this.profile.nombre_cliente;
      }
    );
}

}
