import { Component, OnInit } from '@angular/core';
import { HttpService } from '../../services/http.service';
import { Router, ActivatedRoute } from '@angular/router';
import { ToastrService } from 'ngx-toastr';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css'],
  providers: [HttpService]
})
export class LoginComponent implements OnInit {

  public user = {
    user_name : "",
    password : ""
  }

  constructor(private _route: ActivatedRoute, private _router: Router, private http : HttpService, private toastr : ToastrService) { }

  ngOnInit() {
  }

  auth(){
    const user_data = this.user;
    if(user_data.user_name === "" || user_data.password === ""){
      this.toastr.error('Complete los campos por favor.');
    }else{
      this.http.SignIn(user_data).subscribe(
        data => {
          const access_token = data.access_token;
          localStorage.setItem('access_token', JSON.stringify(access_token));
          this._router.navigate(['/home']);
        },
        err => {
          this.toastr.error('Credenciales incorrectas');
        }
      );
    }
  }
  
  keyupEnter(e) {
    this.auth();
  }

}
