import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { HomeComponent } from './complements/home/home.component';
import { LoginComponent } from './complements/login/login.component';
import { AgregarPuntosComponent } from './complements/puntos/agregar-puntos/agregar-puntos.component';
import { AddUsersComponent } from './complements/users/add-users/add-users.component';
import { ListUsersComponent } from './complements/users/list-users/list-users.component';
import { ListarPuntosComponent } from './complements/puntos/listar-puntos/listar-puntos.component';
import { LocationMapComponent } from './complements/ubications/location-map/location-map.component';
import { ListarRolesComponent } from './complements/roles/listar-roles/listar-roles.component';
import { MiPerfilComponent } from './complements/users/mi-perfil/mi-perfil.component';

/* Privileges */
import { Privilege } from './services/privilege.service';

const routes: Routes = [
  { path: 'login', component: LoginComponent },
  { path: 'home', component: HomeComponent, canActivate:[Privilege] },
  { path: 'agregar-puntos', component: AgregarPuntosComponent, canActivate:[Privilege] },
  { path: 'mi-perfil', component: MiPerfilComponent },
  { path: 'agregar-usuarios', component: AddUsersComponent },
  { path: 'listar-usuarios', component: ListUsersComponent, canActivate:[Privilege] },
  { path: 'listar-puntos', component: ListarPuntosComponent, canActivate:[Privilege] },
  { path: 'ubicaciones', component: LocationMapComponent, canActivate:[Privilege] },
  { path: 'listar-roles', component: ListarRolesComponent },
  { path: '**', pathMatch: 'full', redirectTo: 'login' },
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
