import { Injectable } from '@angular/core';
import { Router, RouterModule, CanActivate } from '@angular/router';
import { ReturnStatement } from '@angular/compiler';
import { Http, Response, Headers } from '@angular/http';
import 'rxjs/add/operator/map';
import { Location } from '@angular/common';
import { GLOBAL } from './global';

@Injectable()
export class Privilege implements CanActivate {

  public url : string;
  public routing = [];

  constructor( private _router: Router, private _route: RouterModule, private http: Http, private location: Location )
  { this.url = GLOBAL.url; this.routing = GLOBAL.routing; }

  canActivate() {
    const token = JSON.parse(localStorage.getItem('access_token'));
    if(token){
      this.decoded().subscribe(
        data => {
          const role = data.token.roleId;
          const privilege_url = this._router.url;
          const approved = this.routing.filter(e => e.link == privilege_url );
          console.log(approved);
          if(approved[0].role === role){
            return true;
          }else{
            this._router.navigate(['/home']);
            return false;
          }
        },
        error => { this._router.navigate(['/login']); return false; }
      );
      return true;
    }else{
      this._router.navigate(['/login']);
      return true;
    }
  }

  decoded(){
    const token = JSON.parse(localStorage.getItem('access_token'));
    const headers = new Headers({ 'Content-Type': 'application/json', 'Accept': 'application/json', 'Authorization': token });
    return this.http.get(this.url + '/token', { headers: headers }).map(res => res.json());
  }

}