import { Component, OnInit } from '@angular/core';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { AddClientModalComponent } from '../add-client-modal/add-client-modal.component';
import { HttpService } from '../../../services/http.service';
import { Router, ActivatedRoute } from '@angular/router';
import { DatePipe } from "@angular/common";
import { ToastrService } from 'ngx-toastr';
import {Observable} from 'rxjs';
import {debounceTime, distinctUntilChanged, map} from 'rxjs/operators';

@Component({
  selector: 'app-add-users',
  templateUrl: './add-users.component.html',
  styleUrls: ['./add-users.component.css'],
  providers: [HttpService, DatePipe]
})
export class AddUsersComponent implements OnInit {

  public user = {
    first_name: "",
    last_name: "",
    type_document: "DNI",
    num_document: "0000",
    people_address: "0000",
    phone: "0000",
    email: "",
    user_name: "",
    password: "",
    confirm_password: "",
    user_start: "",
    user_end: "",
    roleId: "",
    nombre_cliente:""
  };
  public clientName;
  public client = {
    first_name: "",
    last_name: "test",
    type_document: "",
    num_document: "",
    people_address: "",
    phone: "",
    email: "",
  };
  public selectClient = [];
  public filterClient = [];
  public model: any;
  public listRoles = [];

  search = (text$: Observable<string>) =>
    text$.pipe(
      debounceTime(200),
      distinctUntilChanged(),
      map(term => term.length < 2 ? []
        : this.filterClient.filter(v => v.toLowerCase().indexOf(term.toLowerCase()) > -1).slice(0, 10)));

  constructor( public modalService : NgbModal, private _route : ActivatedRoute, private _router : Router, private http : HttpService, private date: DatePipe, private toastr : ToastrService ) { }
  
  ngOnInit() {
    this.listClient();
    this.listRole();
  }
  
  addClientModal() {
    const modalAddRef = this.modalService.open(AddClientModalComponent,{size:'lg'} );
    modalAddRef.result.then((result) => {
      if (result) {
        console.log(result);
      }
    });
  }
  
  saveClient(){
    this.http.saveClient(this.client).subscribe(
        data => {
            this.cleanClient();
            this.listClient();
            this.toastr.success('Nuevo cliente agregado');
        }
    );
  }
  
  listClient(){
    this.http.listClient().subscribe(
        result => {
            this.selectClient = result.data;
            for(let i = 0; i < this.selectClient.length; i++){
                this.filterClient.push(this.selectClient[i].first_name);
            }
        }
    );
  }
  
  listRole(){
    this.http.listRole().subscribe(
        data => {
            this.listRoles = data.data;
        }
    );
  };
   
  cleanClient(){
    this.client.first_name = "";
    this.client.last_name = "";
    //this.client.type_document = "";
    this.client.num_document = "";
    this.client.people_address = "";
    this.client.phone = "";
    this.client.email = "";
  };
   
  saveUser(){
    var date = new Date();
    var dateCurrent = this.date.transform(date, "yyyy-MM-dd");
    const first_name = this.user.first_name;
    const last_name = this.user.last_name;
    const user_name = this.user.user_name;
    const email = this.user.email;
    const password = this.user.password;
    const confirm_password = this.user.confirm_password;
    const user_end = this.user.user_end;
    const roleId = this.user.roleId;
    console.log(this.user);
    if(user_name === "" || password === "" || confirm_password === "" || user_end === "" || roleId === "" || this.clientName === undefined || first_name === "" || last_name === "" || email === ""){
        console.log('Por favor complete los campos');
        this.toastr.error('Por favor complete todos los campos');
    }else{
        if(password != confirm_password){
            console.log('Las contraseñas no coinciden');
            this.toastr.error('Las contraseñas no coinciden');
        }else{
            var date_end = this.user.user_end;
            // @ts-ignore
            var format_date = date_end.year + "-" + date_end.month + "-" + date_end.day;
            this.http.searchClient(this.clientName).subscribe(
                result => {
                    console.log(result.data[0]);
                    if(result.data[0] === undefined){
                        this.toastr.error('Al parecer el cliente no existe, por favor registre uno nuevo');
                    }else{
                        const array = result.data[0];
                        this.user.nombre_cliente = array.first_name;
                        this.user.user_start = dateCurrent;
                        this.user.user_end = format_date;
                        console.log(this.user);
                        this.http.saveUser(this.user).subscribe(
                            data => {
                                console.log(data);
                                this.toastr.success('Usuario agregado correctamente');
                                this.cleanUser();
                            },
                            error => {
                                this.toastr.error(JSON.parse(error._body).message);
                            }
                        );
                    }
                }
            );
        }
    }
  }
  
  cleanUser(){
    this.user.first_name = "";
    this.user.last_name = "";
    this.user.user_name = "";
    this.user.email = "";
    this.user.password = "";
    this.user.confirm_password = "";
    this.user.user_end = "";
    this.user.roleId = "";
  }
    
}
