import { Component, OnInit } from '@angular/core';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { Router, ActivatedRoute } from '@angular/router';
import { AddClientModalComponent } from '../add-client-modal/add-client-modal.component';
import { HttpService } from '../../../services/http.service';

@Component({
  selector: 'app-list-users',
  templateUrl: './list-users.component.html',
  styleUrls: ['./list-users.component.css'],
  providers: [HttpService]
})

export class ListUsersComponent implements OnInit {

  public listUsers = [];
  pages: number = 1;
  constructor(private _route: ActivatedRoute, private _router: Router, private http : HttpService) { }

  ngOnInit() {
    this.listUser();
    this.http.decodedToken().subscribe(
        data => {
            console.log(data);
        }
    );
  }
  
  navegation(action){
    if(action == "add-role"){
        this._router.navigate(['/listar-roles']);
    }else if(action == "add-user"){
        this._router.navigate(['/agregar-usuarios']);
    }
  }

  listUser(){
      this.http.listUser().subscribe(
        data => {
            console.log(data.data);
            this.listUsers = data.data;
        }
      );
  }
 
}
