import { Injectable } from '@angular/core';
import { Http, Response, Headers } from '@angular/http';
import 'rxjs/add/operator/map';
import { Observable } from 'rxjs/Observable';
import { GLOBAL } from './global';

@Injectable({
  providedIn: 'root'
})
export class HttpService {

  public url: string;
  public token;

  constructor(private http: Http) {
    this.url = GLOBAL.url;
  }

  /*************** Auth ***************/
  
  SignIn(data) {
    const headers = new Headers({ 'Content-Type': 'application/json' });
    return this.http.post(this.url + '/signin', data, { headers: headers }).map(res => res.json());
  }

  /*************** Privilege ***************/

  Privileges(id) {
    this.getTokenUser();
    const headers = new Headers({ 'Content-Type': 'application/json', 'Accept': 'application/json', 'Authorization': this.token });
    return this.http.get(this.url + '/role-privilege/' + id, { headers: headers }).map(res => res.json());
  }
  
  /*************** Role ***************/
  
  createRole(data) {
    console.log(data);
    this.getTokenUser();
    const headers = new Headers({ 'Content-Type': 'application/json', 'Accept': 'application/json', 'Authorization': this.token });
    return this.http.post(this.url + '/role', data, { headers: headers }).map(res => res.json());
  }
  
  listRole(){
    this.getTokenUser();
    const headers = new Headers({ 'Content-Type': 'application/json', 'Accept': 'application/json', 'Authorization': this.token });
    return this.http.get(this.url + '/role', { headers: headers }).map(res => res.json());
  }
  
  deleteRole(idRole){
    this.getTokenUser();
    const headers = new Headers({ 'Content-Type': 'application/json', 'Accept': 'application/json', 'Authorization': this.token });
    return this.http.delete(this.url + '/role/' + idRole, { headers: headers }).map(res => res.json());
  }

  /*************** User ***************/

  saveUser(data) {
    this.getTokenUser();
    const headers = new Headers({ 'Content-Type': 'application/json', 'Accept': 'application/json', 'Authorization': this.token });
    return this.http.post(this.url + '/signup', data, { headers: headers }).map(res => res.json());
  }
  
  listUser() {
    this.getTokenUser();
    const headers = new Headers({ 'Content-Type': 'application/json', 'Accept': 'application/json', 'Authorization': this.token });
    return this.http.get(this.url + '/user', { headers: headers }).map(res => res.json());
  }

  profileUser(id){
    this.getTokenUser();
    const headers = new Headers({ 'Content-Type': 'application/json', 'Accept': 'application/json', 'Authorization': this.token });
    return this.http.get(this.url + '/user/'+ id, { headers: headers }).map(res => res.json());
  }
    
  /*************** Role ***************/
  
  saveClient(data) {
    this.getTokenUser();
    const headers = new Headers({ 'Content-Type': 'application/json', 'Accept': 'application/json', 'Authorization': this.token });
    return this.http.post(this.url + '/people', data, { headers: headers }).map(res => res.json());
  }
  
  listClient(){
    this.getTokenUser();
    const headers = new Headers({ 'Content-Type': 'application/json', 'Accept': 'application/json', 'Authorization': this.token });
    return this.http.get(this.url + '/people', { headers: headers }).map(res => res.json());
  }

  getClient(id){
    this.getTokenUser();
    const headers = new Headers({ 'Content-Type': 'application/json', 'Accept': 'application/json', 'Authorization': this.token });
    return this.http.get(this.url + '/people/' + id, { headers: headers }).map(res => res.json());
  }
    
  searchClient(name){
    this.getTokenUser();
    const headers = new Headers({ 'Content-Type': 'application/json', 'Accept': 'application/json', 'Authorization': this.token });
    return this.http.get(this.url + '/people/search/' + name, { headers: headers }).map(res => res.json());
  }  
  /*************** Token Decoded ***************/
  
  decodedToken() {
    const token = JSON.parse(localStorage.getItem('access_token'));
    const headers = new Headers({ 'Content-Type': 'application/json', 'Accept': 'application/json', 'Authorization': token });
    return this.http.get(this.url + '/token', { headers: headers }).map(res => res.json());
  }

  getTokenUser() {
    const _token = "Bearer " + JSON.parse(localStorage.getItem('access_token'));
    if (_token !== 'undefined') {
      this.token = _token;
    } else {
      this.token = null;
    }
    return this.token;
  }

}
