import { Component, OnInit } from '@angular/core';
import { HttpService } from '../../../services/http.service';
import { Router, ActivatedRoute } from '@angular/router';
import { GLOBAL } from '../../../services/global';

@Component({
  selector: 'app-sidebar',
  templateUrl: './sidebar.component.html',
  styleUrls: ['./sidebar.component.css'],
  providers: [HttpService]
})
export class SidebarComponent implements OnInit {

  public sidebar_list = [];
  public dataUser = {
    "id": 0,
    "user_name": "",
    "roleId": 0
  }

  constructor(private _route: ActivatedRoute, private _router: Router, private http : HttpService) { }

  ngOnInit() {
    this.init();
  }

  init(){
    this.http.decodedToken().subscribe(
      data => {
        this.dataUser.id = data.token.id;
        this.dataUser.user_name = data.token.user_name;
        this.dataUser.roleId = data.token.roleId;
        this.privileges();
      }
    );
  }
  
  privileges(){
    const role_id = this.dataUser.roleId;
    const array = GLOBAL.routing;
    const filter = array.filter(e => e.role == role_id);
    this.sidebar_list = filter;
  }
  
  navegation(link){
    this._router.navigate([link]);
  }

}
