import { Component, OnInit } from '@angular/core';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { AddModalContentComponent} from '../add-modal-content/add-modal-content.component'
import { EditModalContentComponent } from '../edit-modal-content/edit-modal-content.component';
import { HttpService } from '../../../services/http.service';
import { Router, ActivatedRoute } from '@angular/router';
import { ToastrService } from 'ngx-toastr';

@Component({
    selector: 'app-listar-roles',
    templateUrl: './listar-roles.component.html',
    styleUrls: ['./listar-roles.component.css'],
    providers: [HttpService]
})
export class ListarRolesComponent implements OnInit {

    public rol = {
    role_name: ""
    };
    pages: number = 1;

    public user = {
    name: 'Izzat Nadiri',
    age: 26
    };

    public listRoles = [];

    constructor( public modalService: NgbModal, private _route: ActivatedRoute, private _router: Router, private http : HttpService, private toastr : ToastrService ) { }

    ngOnInit() {
        this.listRole();
    }

    openModal() {
        const modalRef = this.modalService.open(AddModalContentComponent );
        modalRef.componentInstance.user = this.user;
        modalRef.result.then((result) => {
          if (result) {
            console.log(result);
          }
        });
    }

    asignarPrivilegiosModal(){
        const asignarModalRef = this.modalService.open(EditModalContentComponent,{size: 'lg'});
        asignarModalRef.result.then((result) =>{
          if (result) {
            console.log(result);
          }
        });
    };

    saveRole(){
        this.http.createRole(this.rol).subscribe(
            data => {
                this.toastr.success('Rol agregado correctamente');
                this.cleanRole();
                this.listRole();
            }
        );
    };

    listRole(){
        this.http.listRole().subscribe(
            data => {
                this.listRoles = data.data;
            }
        );
    };

    deleteRole(id){
        if (confirm('¿Seguro que desea eliminar?')) {
          this.http.deleteRole(id).subscribe(
            data => {
                this.toastr.success('Rol eliminado');
                this.listRole();
            },
            error => {
                this.toastr.error('Error al eliminar');
            }
          );
        }
    };
    
    cleanRole(){
        this.rol.role_name = "";
    }
  
}
